body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 15px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,body {
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    /* overflow-x: hidden; */
}
.calendar {
  display: grid;
  grid-template-columns: repeat(7, minmax(120px, auto));
  grid-column-gap: 35px;
  padding: 20px 60px 40px 60px; }

.calendar__slot_day_0 {
  grid-column: 7; }

.calendar__slot_day_1 {
  grid-column: 1; }

.calendar__slot_day_2 {
  grid-column: 2; }

.calendar__slot_day_3 {
  grid-column: 3; }

.calendar__slot_day_4 {
  grid-column: 4; }

.calendar__slot_day_5 {
  grid-column: 5; }

.calendar__slot_day_6 {
  grid-column: 6; }

.calendar__slot_label {
  grid-row: 1; }

.calendar__slot_morning {
  grid-row: 2; }

.calendar__slot_day {
  grid-row: 3; }

.calendar__slot_evening {
  grid-row: 4; }

.calendar__dow_title {
  font-weight: normal;
  font-size: 1em; }

.calendar__date {
  color: #f00;
  font-weight: bold; }

.calendar__event {
  border-top: 1px solid black;
  padding-left: 15px;
  position: relative;
  margin-bottom: 35px; }

.calendar__event:after {
  padding: 0;
  margin: 0;
  display: block;
  content: '';
  height: 86px;
  width: 1px;
  border-left: 1px solid black;
  position: absolute;
  top: -1px;
  left: -1px; }

.calendar__slot_morning .calendar__event:first-child {
  border-top: 2px solid #f00; }

.calendar__slot_morning .calendar__event:first-child:after {
  border-left: 2px solid #f00;
  top: -2px;
  height: 87px; }

.calendar__event-description-title,
.calendar__event-title {
  font-weight: bold;
  font-size: 1em; }

.calendar__event-title {
  font-weight: bold;
  font-size: 1em;
  height: 6em;
  overflow: hidden;
  line-height: 1.4;
  margin-top: 10px;
  cursor: pointer; }

.calendar__event-title:hover {
  color: #19cbfa; }

.calendar__event-time {
  position: absolute;
  top: 0px;
  left: 0px;
  -webkit-transform: rotate(-90deg) translate(-17px, -53px);
          transform: rotate(-90deg) translate(-17px, -53px); }

.calendar__event-description_hidden {
  display: none; }

.calendar__event-description {
  position: absolute;
  background-color: #19cbfa;
  width: 280px;
  top: -2px;
  left: -2px;
  z-index: 2;
  padding: 15px 20px;
  margin-bottom: 10px; }

.calendar__slot_day_0
.calendar__event-description,
.calendar__slot_day_6
.calendar__event-description {
  right: 0px;
  left: auto; }

.calendar__event-description-title {
  color: white;
  margin-top: 5px; }

.calendar__event-time_em {
  color: #19cbfa; }

.calendar__event-description-text {
  font-size: .9em;
  line-height: 1.6; }

.calendar__close-event-descr-btn {
  color: white;
  font-weight: bold;
  font-size: 1.3em;
  position: absolute;
  right: 8px;
  top: 4px;
  cursor: pointer; }

.calendar__week-switcher {
  padding-top: 40px;
  padding-left: 60px; }

.calendar__week-switcher > * {
  display: inline-block; }

.calendar__week-title {
  margin: 0;
  font-weight: bold;
  color: #f00;
  margin-left: 10px; }

.calendar__week-switcher-button {
  border: none;
  outline: none;
  cursor: pointer;
  width: 30px;
  height: 30px;
  text-align: center;
  font-size: 1.4em;
  color: black;
  background-color: rgba(0, 0, 0, 0); }

.calendar__week-switcher-button:hover {
  background-color: lightgrey; }

